body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  background-color: #241b0c;
}

span {
  width: 95%;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  position:relative;
  padding: 20px 20px 20px 20px;
}

p {
  width: 95%;
  background-color: rgba(44, 31, 20, 0.55);
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  position:relative;
  padding: 20px 20px 20px 20px;
  box-shadow: 0 0 20px 10px rgba(188, 101, 41, 0.4);
}

.App {
  text-align: center;
  text-shadow: 2px 1px rgba(20, 10, 0, 1);
  padding: 0px 10px 0px 10px;
  background-color: #241b0c;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 2s normal;
          animation: App-logo-spin infinite 2s normal;
  height: 50vmin;
  pointer-events: none;
  padding: 0px 0px 0px 0px;
}

.App-header {
  min-height: 90vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Button {
  background-color: #604070;
  opacity: 0.9;
  display: -webkit-flex;
  display: flex;
  cursor: pointer;
  font-weight: bolder;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  position:relative;
  box-shadow:0 10px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
  padding: 20px 20px 20px 20px;
}

.ButtonNo {
  background-color: #906060;
  opacity: 0.9;
  display: -webkit-flex;
  display: flex;
  cursor: pointer;
  font-weight: bolder;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  position:relative;
  box-shadow:0px 0px 20px rgba(255, 0, 0, 0.6), 0 0 40px rgba(255, 0, 0, 0.1) inset;
  padding: 20px 20px 20px 20px;
}

.ButtonYes {
  background-color: #609060;
  opacity: 0.9;
  display: -webkit-flex;
  display: flex;
  cursor: pointer;
  font-weight: bolder;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  position:relative;
  box-shadow:0px 0px 20px rgba(0, 255, 0, 0.6), 0 0 40px rgba(0, 255, 0, 0.1) inset;
  padding: 20px 20px 20px 20px;
}

.Beer {
  background-color: rgba(22,36,48,0.85);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  opacity: 0.95;
  background-repeat: no-repeat;
  -webkit-align-items: center;
          align-items: center;
  width: 70%;
  position:relative;
  box-shadow:0 5px 3px rgba(0, 0, 0, 0.6), 0 0 40px rgba(50, 50, 30, 0.8) inset;
  padding: 20px 20px 0px 20px;
}

.BeerSnap {
  display: -webkit-flex;
  display: flex;
  width: 30%;
  cursor: pointer;
  -webkit-justify-content: left;
          justify-content: left;
  vertical-align: middle;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  position:relative;
  left: 2%;
}

.BeerDesc {
  display: -webkit-flex;
  display: flex;
  width: 70%;
  text-align: left;
  vertical-align: middle;
  padding-left: 20px;
  -webkit-justify-content: right;
          justify-content: right;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  position:relative;
}

.App-link {
  color: #ffff99;
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
  text-align: left;
}

.Beer-link {
  color: #ffff99;
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
  text-align: left;
}

.YourBeer-link {
  color: #ffff99;
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
  text-align: left;
  font-weight: bolder;
  -webkit-animation: blinker 0.6s linear infinite;
          animation: blinker 0.6s linear infinite;
}

.YourBeerDesc {
  display: -webkit-flex;
  display: flex;
  width: 70%;
  text-align: left;
  vertical-align: middle;
  padding-left: 20px;
  -webkit-justify-content: right;
          justify-content: right;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  position:relative;
}

.Cheers {
  width: 60%;
  position:relative;
}

.BuyThisBeer {
  width: 60%;
  position:relative;
}

@-webkit-keyframes blinker {
  50% {
    opacity: 0.7;
  }
}

@keyframes blinker {
  50% {
    opacity: 0.7;
  }
}

@-webkit-keyframes App-logo-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  25% {
    -webkit-transform: rotate(4deg);
            transform: rotate(4deg);
  }
  50% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  75% {
    -webkit-transform: rotate(-4deg);
            transform: rotate(-4deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

@keyframes App-logo-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  25% {
    -webkit-transform: rotate(4deg);
            transform: rotate(4deg);
  }
  50% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  75% {
    -webkit-transform: rotate(-4deg);
            transform: rotate(-4deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

